/* src/styles/Footer.css */
.footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background-color: #000;
    color: #fff;
    font-size: 0.9em;
  }
  
  .footer-links a {
    margin: 0 10px;
    text-decoration: none;
    color: #fff;
  }
  
  .footer-links a:hover {
    color: #ccc;
  }
  